html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

.icon-arrow-left {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-arrow-right {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-burger {
  width: 1em;
  height: 1em;
  fill: initial;
}

.owl-carousel {
  display: none;
  width: 100%;
  -webkit-tap-highlight-color: transparent;
  position: relative;
  z-index: 1;
}

.owl-carousel .owl-stage {
  position: relative;
  -ms-touch-action: pan-Y;
}

.owl-carousel .owl-stage:after {
  content: ".";
  display: block;
  clear: both;
  visibility: hidden;
  line-height: 0;
  height: 0;
}

.owl-carousel .owl-stage-outer {
  position: relative;
  overflow: hidden;
  -webkit-transform: translate3d(0px, 0px, 0px);
}

.owl-carousel .owl-wrapper {
  -webkit-backface-visibility: hidden;
  -moz-backface-visibility: hidden;
  -ms-backface-visibility: hidden;
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
}

.owl-carousel .owl-item {
  -webkit-backface-visibility: hidden;
  -moz-backface-visibility: hidden;
  -ms-backface-visibility: hidden;
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  position: relative;
  min-height: 1px;
  float: left;
  -webkit-backface-visibility: hidden;
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none;
}

.owl-carousel .owl-item img {
  display: block;
  width: 100%;
}

.owl-carousel .owl-nav.disabled, .owl-carousel .owl-dots.disabled {
  display: none;
}

.owl-carousel .owl-nav .owl-prev, .owl-carousel .owl-nav .owl-next {
  cursor: pointer;
  cursor: hand;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.owl-carousel .owl-dot {
  cursor: pointer;
  cursor: hand;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.owl-carousel.owl-loaded {
  display: block;
}

.owl-carousel.owl-loading {
  opacity: 0;
  display: block;
}

.owl-carousel.owl-hidden {
  opacity: 0;
}

.owl-carousel.owl-refresh .owl-item {
  visibility: hidden;
}

.owl-carousel.owl-drag .owl-item {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.owl-carousel.owl-grab {
  cursor: move;
  cursor: grab;
}

.owl-carousel.owl-rtl {
  direction: rtl;
}

.owl-carousel.owl-rtl .owl-item {
  float: right;
}

.no-js .owl-carousel {
  display: block;
}

.owl-carousel .animated {
  -webkit-animation-duration: 1000ms;
  animation-duration: 1000ms;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}

.owl-carousel .owl-animated-in {
  z-index: 0;
}

.owl-carousel .owl-animated-out {
  z-index: 1;
}

.owl-carousel .fadeOut {
  -webkit-animation-name: fadeOut;
  animation-name: fadeOut;
}

@-webkit-keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

.owl-height {
  transition: height 500ms ease-in-out;
}

.owl-carousel .owl-item .owl-lazy {
  opacity: 0;
  transition: opacity 400ms ease;
}

.owl-carousel .owl-item img.owl-lazy {
  -webkit-transform-style: preserve-3d;
  transform-style: preserve-3d;
}

.owl-carousel .owl-video-wrapper {
  position: relative;
  height: 100%;
  background: #000;
}

.owl-carousel .owl-video-play-icon {
  position: absolute;
  height: 80px;
  width: 80px;
  left: 50%;
  top: 50%;
  margin-left: -40px;
  margin-top: -40px;
  background: url("owl.video.play.png") no-repeat;
  cursor: pointer;
  z-index: 1;
  -webkit-backface-visibility: hidden;
  transition: -webkit-transform 100ms ease;
  transition: transform 100ms ease;
}

.owl-carousel .owl-video-play-icon:hover {
  -webkit-transform: scale(1.3, 1.3);
  -ms-transform: scale(1.3, 1.3);
  transform: scale(1.3, 1.3);
}

.owl-carousel .owl-video-playing .owl-video-tn, .owl-carousel .owl-video-playing .owl-video-play-icon {
  display: none;
}

.owl-carousel .owl-video-tn {
  opacity: 0;
  height: 100%;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: contain;
  transition: opacity 400ms ease;
}

.owl-carousel .owl-video-frame {
  position: relative;
  z-index: 1;
  height: 100%;
  width: 100%;
}

@font-face {
  font-family: helvetica-l;
  src: url("../fonts/helveticaneuecyr-light.woff2") format("woff2"), url("../fonts/helveticaneuecyr-light.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: helvetica-m;
  src: url("../fonts/helveticaneuecyr-medium.woff2") format("woff2"), url("../fonts/helveticaneuecyr-medium.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

body {
  font-family: "helvetica-l", sans-serif;
  font-size: 14px;
  line-height: 1.4;
}

.wrapper {
  max-width: 1000px;
  margin: 0 auto;
}

.block {
  display: flex;
}

.header__in {
  max-width: 1196px;
  margin: 0 auto;
  padding-left: 10px;
}

@media only screen and (min-width: 601px) {
  .header__in {
    *zoom: 1;
  }
  .header__in:after {
    content: " ";
    display: table;
    clear: both;
  }
}

.header__desc {
  background-color: #a59864;
  position: relative;
  z-index: 1;
}

.header__pic {
  float: left;
  width: 116px;
  margin-bottom: -5px;
}

.header__pic img {
  max-width: 100%;
}

@media only screen and (max-width: 600px) {
  .header__pic {
    width: 84px;
    height: 168px;
    float: none;
    margin: 0 auto;
  }
}

@media only screen and (max-width: mob-spx) {
  .header__pic {
    width: 68px;
    height: 152px;
  }
}

.header__info {
  float: right;
  width: calc(100% - 116px);
  padding: 24px 40px;
  color: #fff;
  column-count: 3;
  column-width: 30.6%;
  column-gap: 40px;
}

@media only screen and (max-width: 1024px) {
  .header__info {
    column-count: 1;
  }
}

@media only screen and (max-width: 600px) {
  .header__info {
    float: none;
    width: 100%;
    padding: 0 10px 20px 10px;
  }
}

.top-nav {
  background-color: #fff;
  box-shadow: 0px 5px 25px 0px rgba(100, 100, 100, 0.5);
}

.top-nav__bl {
  border-bottom: 1px solid #a59864;
  padding-top: 30px;
}

@media only screen and (max-width: 600px) {
  .top-nav__bl {
    padding-top: 10px;
  }
}

.top-nav__bl_in {
  max-width: 1518px;
  text-align: right;
  *zoom: 1;
}

.top-nav__bl_in:after {
  content: " ";
  display: table;
  clear: both;
}

@media only screen and (max-width: 1366px) {
  .top-nav__bl_in {
    max-width: 1222px;
  }
}

.top-nav__info {
  display: inline-block;
  text-align: center;
}

@media only screen and (max-width: 600px) {
  .top-nav__info {
    float: left;
    margin-left: 10px;
  }
}

.top-nav__phone {
  display: block;
  font-family: "helvetica-m", sans-serif;
  font-size: 18px;
  color: #383430;
  text-decoration: none;
}

@media only screen and (max-width: 600px) {
  .top-nav__phone {
    font-size: 14px;
  }
}

.top-nav__btn {
  display: inline-block;
  padding: 13px 15px;
  background-color: #901313;
}

.top-nav__link {
  font-size: 14px;
  color: #fff;
}

.top-nav__link:hover {
  opacity: 0.8;
}

@media only screen and (max-width: 600px) {
  .top-nav__link {
    font-size: 12px;
  }
}

.top-nav__versions {
  display: inline-block;
  margin-left: 5px;
  margin-right: 20px;
}

.top-nav__version {
  display: block;
  font-size: 14px;
  color: #a6a6a6;
  text-decoration: none;
}

.top-nav__burger {
  display: none;
  width: 50px;
  height: 50px;
  margin-right: 10px;
  background-color: transparent;
  border: 1px solid #a59864;
}

.top-nav__burger .icon {
  width: 100%;
  height: 100%;
  stroke: #a59864;
  stroke-width: 5;
}

@media only screen and (max-width: 768px) {
  .top-nav__burger {
    display: inline-block;
  }
}

.logo {
  position: absolute;
  top: 0;
  left: 50%;
  z-index: 999;
  transform: translate(-50%);
  display: block;
  width: 200px;
  height: 200px;
  background-color: #a59864;
  border: 1px solid #fff;
  border-top: none;
}

.logo__img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: block;
  width: 154px;
  height: 50px;
}

@media only screen and (max-width: 600px) {
  .logo__img {
    width: 100px;
    height: 25px;
  }
}

.logo__el {
  fill: #fff;
}

@media only screen and (max-width: 1024px) {
  .logo {
    left: 0;
    top: 0;
    transform: none;
    height: 98px;
    background-color: transparent;
    border: none;
  }
  .logo__el {
    fill: #a59864;
  }
}

@media only screen and (max-width: 600px) {
  .logo {
    display: none;
  }
}

.top-menu {
  max-width: 1080px;
  margin: 0 auto;
  text-align: center;
  *zoom: 1;
}

.top-menu:after {
  content: " ";
  display: table;
  clear: both;
}

.top-menu__col {
  width: 40.74074%;
}

.top-menu__col_l {
  float: left;
}

@media only screen and (max-width: 1024px) {
  .top-menu__col_l {
    margin-right: -20px;
  }
}

.top-menu__col_r {
  float: right;
}

@media only screen and (max-width: 1024px) {
  .top-menu__col {
    float: none;
    display: inline-block;
    width: 49%;
  }
}

@media only screen and (max-width: 768px) {
  .top-menu__col {
    display: block;
    width: 100%;
  }
}

.top-menu__item {
  display: inline-block;
  padding: 22px 0;
  width: 32%;
}

@media only screen and (max-width: 440px) {
  .top-menu__item {
    width: 100%;
    display: block;
    padding: 15px 0;
  }
}

.top-menu__link {
  color: #a59864;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 12px;
}

.top-menu__link:hover, .top-menu__link.is-active {
  color: #191821;
}

.top-slider {
  position: relative;
}

.top-slider__slide {
  width: 100%;
}

.top-slider__slide_img {
  max-width: 100%;
}

.top-slider__arr {
  display: block;
  background-color: transparent;
  position: absolute;
  top: 50%;
  transform: translate(0, -50%);
  z-index: 5;
  opacity: 0.8;
}

.top-slider__arr .icon {
  width: 50px;
  height: 50px;
  fill: #fff;
}

.top-slider__arr_prev {
  left: 0;
}

.top-slider__arr_next {
  right: 0;
}

.top-slider__arr:hover {
  opacity: 1;
}
